import {postRequest,putRequest,deleteRequest,getRequest,postKeyValueRequest} from "../utils/request";

export const listBazaar = (params) => {
    return postRequest("/uiapi/v1/bazaar/list", params)
}

export const createBazaar = (params) => {
    return postRequest("/uiapi/v1/bazaar/add", params)
}

export const deleteBazaar = (params) => {
    return deleteRequest("/uiapi/v1/bazaar/delete", params)
}

export const updateBazaar = (params) => {
    return putRequest("/uiapi/v1/bazaar/update", params)
}