<template>
    <div style="margin-top: 40px">
        <el-form :inline="true" :model="queryParam" class="demo-form-inline">
            <el-form-item label="市场名称">
                <el-input v-model="queryParam.name" placeholder="名称" clearable></el-input>
            </el-form-item>
            <el-form-item label="市场地址">
                <el-input v-model="queryParam.conditions.location" placeholder="地址" clearable></el-input>
            </el-form-item>
            <el-form-item label="市场状态">
                <el-select v-model="queryParam.conditions.status" placeholder="市场状态" clearable @clear="selectClear('status')">
                    <el-option label="维修中" value="0"></el-option>
                    <el-option label="开放中" value="1"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
        </el-form>

        <el-button type="primary" @click="createBazaar">创建</el-button>

        <el-table
                :data="listData"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="name"
                    label="市场名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="phone"
                    label="市场电话"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="manager"
                    label="市场管理员"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="location"
                    label="市场地址">
            </el-table-column>
            <el-table-column
                    prop="sortNumber"
                    label="排序">
            </el-table-column>
            <el-table-column
                    prop="status"
                    label="市场状态"
                    :formatter="formatStatus"
            >
            </el-table-column>

            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button type="text" @click="update(scope.row)">修改</el-button>
                    <el-button type="text" @click="doDelete(scope.row)">删除</el-button>
                </template>

            </el-table-column>
        </el-table>
        <el-row :gutter="20" style="margin-top: 25px">
            <el-col :span="4" :offset="18">
                <el-pagination
                        background
                        :layout=pageInfo.layout
                        :total="pageInfo.total">
                </el-pagination>
            </el-col>
        </el-row>


        <el-dialog
                :title="title"
                :visible.sync="dialogVisible"
                width="40%">
            <el-form ref="form"  :model="formData" label-width="200px" size="medium">
                <el-form-item label="市场名称" prop="name" >
                    <el-input v-model="formData.name" style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item label="市场电话" prop="phone">
                    <el-input v-model="formData.phone" style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item label="市场管理员" prop="manager">
                    <el-select v-model="formData.manager" placeholder="" style="width: 350px">
                        <el-option :label="item.name" :value="item.code" v-for="item in managerList"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="市场地址" prop="location">
                    <el-input v-model="formData.location" style="width: 350px"></el-input>
                </el-form-item>
                <el-form-item label="排序" prop="sortNumber">
                    <el-input v-model="formData.sortNumber" style="width: 350px" ></el-input>
                </el-form-item>
                <el-form-item label="市场状态" prop="status">
                    <el-select v-model="formData.status" placeholder="" style="width: 350px">
                        <el-option :label="item.name" :value="item.code" v-for="item in status"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-if="formData.id" @click="doUpdate">保存</el-button>
                    <el-button type="primary" v-else @click="doCreate">立即创建</el-button>
                    <el-button @click="dialogVisible = false">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

    </div>
</template>

<script>
    import {listBazaar,createBazaar,deleteBazaar,updateBazaar} from "../../../api/bazaarApi";

    export default {
        name: "Bazaars",
        data() {
            return {
                listData: [],
                queryParam: {
                    "pageNumber": 1,
                    "pageSize": 10,
                    "name": null,
                    "conditions": {
                        "location": null,
                        "status": null
                    }

                },
                dialogVisible: false,
                formData: {
                    id: null,
                    name: null,
                    phone: null,
                    manager: null,
                    location: null,
                    sortNumber: 1,
                    status: null
                },
                managerList: [
                    {
                        "name": "SmsAdmin",
                        "code": "SmsAdmin"
                    }
                ],
                status: [
                    {
                        "name": "维护中",
                        "code": 0
                    },
                    {
                        "name": "开放中",
                        "code": 1
                    }
                ],
                rules: {
                    name: [
                        { required: true, message: '必填'}
                    ],
                    phone: [
                        { required: true, message: '必填'}
                    ],
                    manager: [
                        { required: true, message: '必填'}
                    ],
                    location: [
                        { required: true, message: '必填'}
                    ],
                    sortNumber: [
                        { required: true, message: '必填'}
                    ],
                    status: [
                        { required: true, message: '必填'}
                    ]
                },
                pageInfo: {
                    layout: "prev, pager, next, jumper, total",
                    total: 10
                },
                title: "创建市场"
            }
        },
        created() {
            this.listBazaars()
        },
        methods: {
            listBazaars() {
                listBazaar(this.queryParam).then(res => {
                    this.listData = res.data.first
                    this.pageInfo.total = res.data.second
                })
            },
            onSubmit() {
                this.listBazaars()
            },
            // row 行数据，column 列数据
            formatStatus(row, column) {
                if (row.status == 0) {
                    return "维护中"
                } else {
                    return "开放中"
                }
            },
            createBazaar() {
                this.dialogVisible = true
                this.initFormData()
            },
            doCreate() {
                createBazaar(this.formData).then(res => {
                    this.dialogVisible = false
                    this.initFormData()
                   this.$message({
                       message: '创建成功',
                       type: 'success'
                   });
                    this.listBazaars()
                })
            },
            initFormData(row) {
                if (row != null) {
                    this.formData = {
                        id: row.id,
                        name: row.name,
                        phone: row.phone,
                        manager: row.manager,
                        location: row.location,
                        sortNumber: row.sortNumber,
                        status: row.status
                    }
                } else {
                    this.formData = {
                        id: null,
                        name: null,
                        phone: null,
                        manager: null,
                        location: null,
                        sortNumber: 1,
                        status: null
                    }
                }

            },
            doDelete(row) {
                const params = []
                params.push(row.id)
                deleteBazaar(params).then(res => {
                    this.$message({
                        message: '删除成功',
                        type: 'success'
                    });
                    this.listBazaars()
                })
            },
            update(row) {
                this.title = "编辑市场"
                this.initFormData(row);
                this.dialogVisible = true
            },
            doUpdate() {
                updateBazaar(this.formData).then(res => {
                    this.dialogVisible = false
                    this.initFormData()
                    this.$message({
                        message: '修改成功',
                        type: 'success'
                    });
                    this.listBazaars()
                }).catch(error =>{
                    this.dialogVisible = false
                    this.initFormData()
                    this.$message({
                        message: '修改失败',
                        type: 'error'
                    });
                    this.listBazaars()
                } )
            },
            selectClear(data) {
                this.queryParam.conditions[data] = null
            }
        }
    }
</script>

<style>

</style>